
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
}

#container-video-larkplayer video {
  position: relative;
}
#AD-video {
  position: absolute;
  width: 60%;
  height: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}
#AD-video .ad-span {
  position: absolute;
  background-color: #333;
  color: #fff;
  padding: 3px 5px;
  font-size: 14px;
}
#AD-video .ad-text {
  left: 5px;
  top: 5px;
}
#AD-video .ad-close {
  padding: 2px 7px;
  font-size: 16px;
  top: 5px;
  right: 5px;
}
